@import '../../variables.scss';
@import '../../mixin.scss';

.marketWrap {
    dd, dt, dl {
        margin: 0;
        padding: 0;
    }
    .marketTitle {
        width: 100%;
        text-align: center;
        font-weight: 700;
        line-height: 43.2px;
        font-size: 36px;
        color: $DarkBlack;
    }
    .marketTotal {
        margin-top: 32px;
        display: flex;
        flex-direction: row;
        align-items: stretch;
        :global {
            .col-sm-12 {
                padding-left: 10px;
                padding-right: 10px;
            }
        }
        dt {
            font-size: 14px;
            font-weight: 400;
            line-height: 16.41px;
            color: $DarkenGrey;
        }
        dd {
            .num {
                color: $DarkBlack;
            }
            .percentNum {
                font-size: 20px;
                font-weight: 500;
                line-height: 24px;
                &.add {
                    color: $Green;
                }
                &.sub {
                    color: $Red;
                }
            }
        }

        .totalSize {
            background: $PureWhite;
            border-radius: 16px;
            padding: 50px 30px;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            dd {
                display: flex;
                flex-direction: column;
                .num {
                    font-weight: 600;
                    font-size: 40px;
                    line-height: 46.88px;
                    margin-top: 20px;
                }
                .percentNum {
                    margin-top: 15px;
                }
            }
        }
        .totalSupplyBorrow {
            display: flex;
            flex-direction: column;
            height: 100%;
            dl {
                display: flex;
                flex-direction: column;
                justify-content: center;
                &:first-child {
                    margin-bottom:10px;
                    border-left: 6px solid $Green;
                }
                &:last-child {
                    margin-top: 10px;
                    border-left: 6px solid $Blue;
                }
                background: $PureWhite;
                border-radius: 12px;
                padding: 24px 30px;
                height: 50%;
                dd {
                    margin-top: 8px;
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;
                    align-items: center;
                    .num {
                        font-weight: 600;
                        font-size: 28px;
                        line-height: 32.81px;
                        margin-right: 20px;
                    }
                }
            }
        }
    }
    .marketTable {
        margin-top: 40px;
        .loading {
            height: 150px;
            td {
                border-bottom-width: 0;
            }
        }
        .noData {
            height: 150px;
            text-align: center;
            line-height: 1.28;
            font-size: 1rem;
            vertical-align: middle;
            td {
                border-bottom-width: 0;
            }
        }
        .tabMenuContainer {
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;
            .switchChain {
                margin-right: 20px;
            }
            .tabMenuItem {
                padding: 10px 20px;
                border-radius: 4px;
                color: $DarkBlack;
                font-weight: 300;
                @include Barlow;
                font-size: 14px;
                line-height: 16.8px;
                transition: all .2s ease;
                &:hover {
                    // font-weight: 600;
                    cursor: pointer;
                    transform: translateY(-2px);
                }
                &.active {
                    font-weight: 700;
                    color: $PureWhite;
                    background: $Green;
                    border-radius: 24px;
                    line-height: 16.41px;
                        
                    // &:hover {
                    //     transform: translateY(-2px);
                    // }
                }
            }
        }
        .totalMarketData {
            display: flex;
            flex-direction: row;
            justify-content: flex-end;
            align-items: center;
            .label {
                font-size: 14px;
                font-weight: 400;
                line-height: 1.14;
                color: $DarkenGrey;
            }
            .value {
                font-size: 28px;
                font-weight: 600;
                color: $DarkBlack;
                margin-left: 23px;
                cursor: pointer;
            }
        }
        .marketTableContainer {
            margin-top: 24px;
            background-color: $PureWhite;
            border-radius: 16px;
            padding: 0 20px;
            thead {
                tr {
                    th {
                        padding: 24px 10px;
                        font-size: 14px;
                        font-weight: 400;
                        color: $DarkenGrey;
                        text-align: right;
                        border-bottom: 1px solid $LineGrey;
                        &:first-child {
                            text-align: left;
                        }
                        &.actions {
                            text-align: center;
                        }

                        .titleTxt {
                            position: relative;
                            display: inline-block;
                            padding-right: 16px;
                            cursor: pointer;
                            user-select: none;
                            text-align: right;
                            white-space: nowrap;
                            &:before {
                                content: '';
                                position: absolute;
                                top: calc(50% - 9px);
                                right: 0;
                                border: 4px solid $LightenGrey3;
                                border-top: 4px solid transparent;
                                border-left: 4px solid transparent;
                                border-right: 4px solid transparent;
                                border-radius: 2px;
                            }
                            &:after {
                                content: '';
                                position: absolute;
                                bottom:calc(50% - 9px);
                                right: 0;
                                border: 4px solid $LightenGrey3;
                                border-bottom: 4px solid transparent;
                                border-left: 4px solid transparent;
                                border-right: 4px solid transparent;
                                border-radius: 2px;
                            } 
                            &.down:after {
                                border-top-color: $Green;
                                opacity: 1;
                            }
                            &.up::before {
                                border-bottom-color: $Green;
                                opacity: 1;
                            }
                        }
                    }
                }
            }
            tbody {
                .marketRow {
                    background: $PureWhite;
                    cursor: pointer;
                    &:hover {
                        background-color: $Bg;
                    }
                    td {
                        padding: 13px 10px;
                        vertical-align: middle;
                        border-bottom: 1px solid $LineGrey;
                        &:first-child {
                            width: 200px;
                        }
                        .asset {
                            display: flex;
                            flex-direction: row;
                            justify-content: flex-start;
                            align-items: center;
                            .name {
                                margin-left: 13px;
                                font-size: 16px;
                                color: $DarkBlack;
                                font-weight: 500;
                            }
                        }
                        .dataContainer {
                            display: flex;
                            flex-direction: column;
                            justify-content: flex-end;
                            align-items: flex-end;
                            .dataPrimary {
                                font-size: 18px;
                                font-weight: 500;
                                color: $DarkBlack;
                                text-align: right;
                            }
                            .dataSecondary {
                                font-size: 14px;
                                color: $Grey;
                                font-weight: 400;
                                text-align: right;
                            }
                            >button {
                                white-space: nowrap;
                            }
                        }
                        &.actionsTd {
                            cursor: not-allowed;
                        }
                    }
                }
            }
        }
    }
}




.toolTipList {
    .toolTipItem {
        margin: 5px 0;
        display:flex;
        flex-direction: row;
        align-items: center;
        .hd {
            text-align: right;
            color: $Grey;
            font-weight: 500;
            font-size: 12px;
        }
        .bd {
            margin-left: 5px;
            text-align: left;
            color: $DarkBlack;
            font-weight: 500;
            font-size: 16px;
        }
    }
}



@media (max-width: 991.98px) { 
    .marketWrap {
        .marketTable {
            .totalMarketData {
                width: 100%;
                justify-content: center;
                order: 2;
            }
            .tabMenuContainer {
                width: 100%;
                flex-direction: column;
                justify-content: center;
                order: 1;
                .btnGroup {
                    order: 2;
                    margin-top: 10px;
                    margin-bottom: 10px;
                }
                .switchChain {
                    margin-right: 0;
                    order: 1;
                }
            }
        }
    }
}



@media (min-width: 576px) and (max-width: 767.98px) {
    .marketWrap {
        .marketTotal {
            .totalSupplyBorrow {
                flex-direction: row;
                margin-top: 20px;
                dl {
                    flex: 1;
                    height: 100%;
                    &:first-child {
                        margin-bottom: 0;
                        margin-right: 10px;
                    }
                    &:last-child {
                        margin-top: 0;
                        margin-left: 10px;
                    }
                }
            }
        }
    }
}

@media (max-width: 575.98px) {
    .marketWrap {
        .marketTotal {
            .totalSupplyBorrow {
                margin-top: 20px;
            }
        }
    }
}